import Input from "@/app/components/ui/Input";
import { COLORS, TEXT_STYLES } from "@/app/constants";
import Image from "next/image";
import React from "react";
import coffee from "@/public/assets/imgs/7532764.jpg";

const page = () => {
  const offer = {
    id: 1,
    title: "خصم 25% على كل قوائم القهوة",
    description: "تعديل بيانات حسابك الشخصي",
    expiry: "1 يوم",
  };

  return (
    <div className="mt-20 px-4 text-right">
      <div className="flex flex-col gap-8">
        <Input
          headline="هدايا مميزة لك"
          placeholder="استمتع بالكثير من الهدايا كلما اشتريت مزيد من القهوة"
        />
        <div style={{ ...TEXT_STYLES.READEX_PRO.BOLD.H6, color: COLORS.TEXT }}>
          الكوبنات الخاصة بك
        </div>
        <div>
         <div className="flex">
    <Image/>
          <div
            key={offer.id}
            className="flex flex-col gap-2 mb-6 border-b border-dashed border-gray-300 pb-4"
            >
            {/* Title */}
            <h4 className="font-bold text-lg text-right text-black">
              {offer.title} {/* e.g. "خصم 25% على كل قوائم القهوة" */}
            </h4>

            {/* Description */}
            <p className="text-right text-gray-500">
              {offer.description} {/* e.g. "تعديل بيانات حسابك الشخصي" */}
            </p>

            {/* Expiry */}
            <div className="flex flex-row-reverse items-center gap-1 text-red-500 text-sm">
              <span className="text-xs">ينتهي خلال {offer.expiry}</span>
              <span className="text-red-500">⏰</span>
            </div>
          </div>
            </div>

          <div></div>
        </div>

      </div>
    </div>
  );
};

export default page;
